body {
  font-family: "Open Sans", Helvetica, sans-serif;
  padding: 100px;
  background: rgb(152, 190, 187);
  background: linear-gradient(275deg, rgb(152, 190, 187) 35%, rgb(195, 112, 127) 70%);
}

.main-container {
  -webkit-box-shadow: 9px 12px 13px -10px rgba(0, 0, 0, 0.64);
  -moz-box-shadow: 9px 12px 13px -10px rgba(0, 0, 0, 0.64);
  box-shadow: 9px 12px 13px -10px rgba(0, 0, 0, 0.64);
  border-radius: 20px;
  width: 809px;
  height: 391px;
  background-color: white;
}
.main-container:hover {
  color: white;
  background-color: #575555;
  color: white;
  background-color: #2F2E2E;
}
.main-container .flex {
  display: flex;
}
.main-container .side-panel {
  padding: 10px;
  text-align: center;
  width: 200px;
  height: 391px;
}
.main-container .side-panel:hover {
  color: white;
  background-color: #575555;
  border-radius: 20px;
}
.main-container .side-panel p {
  font-weight: bold;
}
.main-container .right-panel {
  width: 612px;
  height: 391px;
  border-top-right-radius: 20px;
  border-bottom-right-radius: 20px;
  background-color: #E7D6C6;
}
.main-container .right-panel:hover {
  color: white;
  background-color: #2F2E2E;
}
.main-container .right-panel h2 {
  margin-left: 20px;
}
.main-container .right-panel input {
  border-radius: 20px;
  border-style: none;
  width: 209px;
  height: 33px;
  margin-left: 137px;
  margin-top: 20px;
  text-align: right;
}
.main-container .right-panel .big-rectangle {
  width: 184px;
  height: 139px;
  background-color: white;
  border-radius: 20px;
  margin-right: 2px;
  margin-left: 20px;
  text-align: right;
}
.main-container .right-panel .big-rectangle:hover {
  color: white;
  background-color: #575555;
}
.main-container .right-panel .big-rectangle .flex2 {
  display: flex;
  flex-direction: column;
}
.main-container .right-panel .big-rectangle img {
  width: 76px;
  height: 76px;
  position: relative;
  bottom: 56px;
  left: 7px;
}
.main-container .right-panel .big-rectangle p {
  font-weight: bold;
  margin: 0;
  padding: 0;
  margin-right: 10px;
  margin-top: 10px;
}
.main-container .right-panel .big-rectangle h2 {
  margin: 0;
  padding: 0;
  margin-right: 10px;
}
.main-container .right-panel .small-rectangle {
  width: 184px;
  height: 103px;
  background-color: white;
  border-radius: 20px;
  margin-right: 2px;
  margin-top: 20px;
  margin-left: 20px;
  text-align: right;
}
.main-container .right-panel .small-rectangle:hover {
  color: white;
  background-color: #575555;
}
.main-container .right-panel .small-rectangle .flex2 {
  display: flex;
  flex-direction: column;
}
.main-container .right-panel .small-rectangle img {
  width: 60px;
  height: 60px;
  position: relative;
  left: 10px;
  bottom: 40px;
}
.main-container .right-panel .small-rectangle p {
  font-weight: bold;
  margin: 0;
  padding: 0;
  margin-top: 20px;
  margin-right: 15px;
}
.main-container .right-panel .small-rectangle h2 {
  margin: 0;
  padding: 0;
  margin-right: 15px;
}
.main-container .units {
  width: 100%;
  text-align: right;
}
.main-container .units .flex {
  gap: 20px;
  justify-content: end;
  margin-right: 26px;
}
.main-container .units .slider {
  position: relative;
  top: 16px;
  right: 95px;
  cursor: pointer;
  padding: 0;
  margin: 0;
  background-color: #ccc;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}
.main-container .units .green {
  color: green;
}